.card {
  background: #fff;
  border-radius: 2px;
  height: 300px;
  margin: 1rem;
  position: relative;
  width: 300px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

.card > .imgContainer {
  height: 50%;
  overflow: hidden;
  text-align: center;
  background: #6caddc;
  padding: 20px;
}
.card > .imgContainer > img {
  width: 100px;
  height: 100px;
}

img{
  width: 200px;
  height:200px;
}
.wrapper {
    padding: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.title {
    width: 100%;
    text-align: center;
    font-size: 25px;
    color: #2A77AE;
    margin: 50px;
    padding: 50px;
  }

  .jumbotron.jumbotron-fluid{
    background-color: blueviolet;
  }
.footer {
  width: 100%;
  text-align: center;
  font-size: 25px;
  color: #2A77AE;
  margin: 50px;
  padding: 50px;
}

.jumbotron.jumbotron-fluid{
  background-color: blueviolet;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
